html,
body,
.out,
.main,
.vertical-center {
	height: 100%; }

.out {
	position: relative; }

.main {
	position: relative;
	&_inner {
		overflow: auto;
		padding-top: 50px; }
	&_lg {
		padding-top: 150px;
		@include media('height', 'sm') {
			padding-top: 100px; } }
	&_md {
		padding-top: 90px; } }

.vertical-center {
	@include vertical;
	&__container {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		font-size: 16px;
		text-align: left; } }
.section {
	padding: 20px; }

.container {
	overflow: auto;
	padding: 20px;
	height: 100%; }

.row {
	margin: 0 -10px;
	@include clr;
	+ .row {
		padding-top: 20px; } }

.col {
	float: left;
	padding: 0 10px;
	width: 100%;
	&_lg {
		&-20 {
			width: 20%; }
		&-50 {
			width: 50%; } } }
