//adaptive points
@mixin media($direction, $point) {
  @if $direction == 'height' {
    @if $point == 'sm' {
      @include rh(650) {
        @content; } } }
  @if $direction == 'width' {
    @if $point == 'sm' {
      @include r(767) {
        @content; } }
    @if $point == 'md' {
      @include r(980) {
        @content; } }
    @if $point == 'lg' {
      @include r(1200) {
        @content; } } } }

//states
@mixin touch {
  .is-touch & {
    @content; } }

@mixin poynt {
  .is-poynt & {
    @content; } }
