.table-wrapper {
	position: relative;
	overflow: hidden;
	padding-top: 37px;
	height: 100%;
	&_header-lg {
		padding-top: 50px;
		.table-wrapper {
			&__header {
				height: 50px; } } }
	&_adaptive {
		@include media('width', 'md') {
			padding-top: 0; }
		.table-wrapper {
			&__header {
				@include media('width', 'md') {
					display: none; } }
			&__body {
				@include media('width', 'md') {
					border-top: 1px solid $lightGray;
					border-bottom: 1px solid $lightGray; } } }
		.table-header,
		.table-body {
			&__th,
			&__td {
				&[data-column*="action"] {
					@include media('width', 'md') {
						display: none; } } } }
		.table-header {
			@include media('width', 'md') {
				display: none; } }
		.table-body {
			@include media('width', 'md') {
				display: block;
				@include clr; }
			&__tr,
			&__td {
				@include media('width', 'md') {
					display: block; } }
			&__td {
				@include media('width', 'md') {
					position: relative;
					padding: 4px 0 4px 50%;
					border: none; }
				&:before {
					@include media('width', 'md') {
						content: attr(data-title);
						position: absolute;
						top: 0;
						left: 0;
						padding: 4px 20px;
						width: 50%; } } }
			&__tr {
				@include media('width', 'md') {
					float: left;
					padding: 8px 0;
					width: 50%; }
				@include media('width', 'sm') {
					float: none;
					width: 100%; }
				&:nth-child(even) {
					@include media('width', 'md') {
						border-left: 1px solid $lightGray;
						background: none; }
					@include media('width', 'sm') {
						border-left: none;
						background: $white; } } }
			&__tr:not(:first-child) {
				@include media('width', 'md') {
					border-top: 1px solid $lightGray; }
				.table-body__td {
					@include media('width', 'md') {
						border: none; } } } } }
	&__header {
		position: absolute;
		z-index: 2;
		top: 0;
		left: 0;
		right: 0;
		height: 37px;
		&:before {
			content: '';
			position: absolute;
			top: 0;
			left: 100%;
			bottom: 0;
			border-bottom: 1px solid #aaa;
			width: 30px;
			background: #eee;
			margin-left: -10px; } }
	&__body {
		overflow: auto;
		border-bottom: 1px solid $lightGray;
		max-height: 100%; } }

.table-header,
.table-body {
	display: table;
	table-layout: fixed;
	width: 100%;
	font-size: 14px;
	@include media('width', 'lg') {
		font-size: 12px; }
	&__tr {
		display: table-row;
		&.is-disabled {
			opacity: 0.3;
			pointer-events: none; } }
	&__th,
	&__td {
		display: table-cell;
		vertical-align: middle;
		padding: 10px;
		word-wrap: break-word;
		@include media('width', 'lg') {
			padding: 10px 4px; }
		&:first-child {
			padding-left: 20px; }
		&[data-column*="orders-status"] {
			width: 80px; }
		&[data-column*="action"] {
			width: 40px; }
		&[data-column*="orders-total"] {
			word-wrap: break-word; } }
	&__th {
		@include rmed;
		line-height: 1.2; } }

.table-header {
	border-bottom: 1px solid $silverGray;
	height: 37px;
	background: $lightGray; }

.table-body {
	&_clickable {
		.table-body {
			&__tr {
				cursor: pointer;
				@include hover {
					background: $azure; } } } }
	&_zebra {
		.table-body {
			&__tr {
				&:nth-child(even) {
					background: $white; } } } }
	&__tr {
		transition: background 0.1s ease-in-out;
		&:not(:first-child) {
			.table-body {
				&__td {
					border-top: 1px solid $lightGray; } } }
		.fa {
			font-size: 20px; } } }
